import React from 'react';

export const Items = [
  {
    img: require('../assets/1.webp'),
  },
  {
    img: require('../assets/2.webp'),
  },
  {
    img: require('../assets/3.webp'),
  },
  {
    img: require('../assets/4.webp'),
  },
  {
    img: require('../assets/5.webp'),
  },
  {
    img: require('../assets/6.webp'),
  },
  {
    img: require('../assets/7.webp'),
  },
  {
    img: require('../assets/8.webp'),
  },
  {
    img: require('../assets/9.webp'),
  },
  {
    img: require('../assets/10.webp'),
  }
]

const Item = (props) => {
  const data = Items[props.match.params.id];
    return (
        <div style={{display: 'flex', height: '100vh', flexDirection: 'column', justifyContent: 'space-between'}}>
            <div style={{height: '300px', backgroundSize: 'cover', backgroundPosition: '50% 50%', padding: '16px', backgroundImage: 'url(' + data.img + ')'}}>
              <a onClick={() => props.history.push('/')} style={{display: 'block', height: '24px', width: "24px", backgroundColor: 'rgba(0,0,0,.5)', borderRadius: '50%', color: "#fff", boxSizing: 'border-box', padding: '4px 6px'}}>{"<"}</a>
            </div>

            <div style={{display: 'flex', justifyContent: 'space-around', padding: '16px'}}>
              <a>店铺</a>
              <a>客服</a>
              <a>收藏</a>
              <div>
              <a style={{borderRadius: '1rem 0 0 1rem', backgroundImage: 'linear-gradient(to right, #FFC500, #FF9402)', padding: '6px 16px', color: '#fff', fontWeight: 'bold'}}>加入购物车</a>
              <a style={{borderRadius: '0 1rem 1rem 0', backgroundImage: 'linear-gradient(to right, #FF7A00, #FE560A)', padding: '6px 16px', color: '#fff', fontWeight: 'bold'}}>立即购买</a>
              </div>
            </div>
        </div>
    )
}

export default Item
