import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
// import Header from './pages/Header';
// import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
import Mytab from './Mytab';
import Home from './Home';
import Cart from './Cart';
import Search from './Search';
import Item from './Item';
// https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg
const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/search' component={Search} />
			<Route path='/home/detail' component={Cart} />
			<Route path='/detail/:id' component={Item} />
			{/* <Route component={Home} /> */}
			<Route render={()=><Redirect to='/'/>} />
		</Switch>
		<Mytab />
	</Router>
}
export default App;


function PrivateRoute({ routes,component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com routes={routes}/>
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}

export const RouteWithSubRoute = (props)=>{
	if(props.pri){
		return <PrivateRoute {...props}/>
	}
	return <Route 
				path={props.path} 
				render={
					(pro)=><props.component {...pro} routes={props.routes}/>
				}
			/>
}